.auth__submit-button {
  width: 100%;
  height: 50px;
  margin-top: calc(100vh - 342px - 50px - 112px);
  background: #ffffff;
  border: none;
  border-radius: 2px;
  justify-self: end;
  font-size: 18px;
  text-align: center;
  color: #000000;
  cursor: pointer;
  transition: ease-out 0.5s;
}

.auth__submit-button:hover {
  opacity: 0.85;
  transition: ease-out 0.5s;
}
