.auth__input {
  width: 100%;
  border: none;
  vertical-align: top;
  border-bottom: 2px solid #cccccc;
  background-color: #000000;
  margin-top: 30px;
  padding: 0;
  font-family: inherit;
  font-size: 14px;
  line-height: 2.6;
  color: #ffffff;
  position: relative;
}

.auth__input::placeholder {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.21;
  color: #cccccc;
}
